.timeline {
  display: flex;
  margin: 65px auto;
  width: 100%;
  max-width: 768px;
  position: relative;
}
@media (max-width: 991px) {
  .timeline {
    margin: 30px auto;
  }
}
.timeline:before {
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  display: block;
  width: 1px;
  height: 100%;
  background-color: #2B2A2C;
}
.timeline__item {
  margin-bottom: 30px;
}
.timeline__item:last-child {
  margin-bottom: 0;
}
.timeline__col {
  width: 50%;
}
.timeline__col--left {
  text-align: right;
  padding-right: 63px;
}
@media (max-width: 991px) {
  .timeline__col--left {
    padding: 0 35px 0 40px;
  }
}
.timeline__col--right {
  padding-left: 63px;
}
@media (max-width: 991px) {
  .timeline__col--right {
    padding: 0 40px 0 35px;
  }
}
.timeline__title {
  font-size: 18px;
  color: #2B2A2C;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 38px;
}
.timeline__date {
  display: block;
  text-decoration: underline;
  font-size: 14px;
  color: #2B2A2C;
  margin-bottom: 12px;
}
.timeline__subtitle {
  font-size: 16px;
  color: #2B2A2C;
  font-weight: 700;
  margin-bottom: 3px;
}
.timeline__position {
  font-size: 14px;
  font-style: italic;
  color: #2B2A2C;
}
.timeline__text {
  font-size: 12px;
  color: #6A696B;
  font-weight: 300;
  line-height: 18px;
  margin-top: 12px;
}
@media (max-width: 991px) {
  .timeline__text {
    font-size: 10px;
  }
}
.progress-bar {
  margin-bottom: 35px;
}
.progress-bar__item {
  display: flex;
  align-items: center;
}
.progress-bar__title {
  font-size: 13px;
  font-weight: 300;
}
@media (max-width: 565px) {
  .progress-bar__title {
    font-size: 13px;
  }
}
.progress-bar__right {
  margin-left: 19px;
}
.progress-bar__left {
  margin-bottom: 5px;
}
.progress-bar__bar {
  width: 139px;
  height: 6px;
  background-color: #EFEFEF;
}
@media (max-width: 565px) {
  .progress-bar__bar {
    width: 90px;
  }
}
.progress-bar__progress {
  height: 100%;
  background-color: #6A696B;
  transition: width 0.2s linear;
}
.contact {
  display: flex;
  background: url(https://via.placeholder.com/490x800) right center no-repeat;
}
